
* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: 0;
  padding: 0;
}
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}










































































































































































































































































/*  ==================================================== */
table {
  border-collapse: collapse;
}
th,
td {
  border: 1px solid black;
  padding: 15px;
}
/*  ==================================================== */
.wrapper-table {
  width: 400px;
  margin: 0 auto;
}
.table-title {
  width: 400px;
}
.table-title__name {
  width: 238px;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  background: #95ecb7;
}
.table-title__phone {
  -webkit-transition: 0.5s;
  transition: 0.5s;
  background: #95ecb7;
}
.table-title__name:hover,
.table-title__phone:hover {
  cursor: pointer;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  background: #30f17a;
}
/*  ========================table parent============================ */
.table-parent {
  width: 400px;
}
.table-parent__name {
  word-break: break-all;
  background: #9bec95;
  width: 238px;
}
.table-parent__phone {
  background: #9bec95;
  word-break: break-all;
}
/*  ============================table child======================== */
.table-child {
  width: 350px;
  margin-left: 50px;
}
.table-child__phone {
  border-top: none;
  word-break: break-all;
}
.table-child__name {
  word-break: break-all;
  width: 188px;
  border-top: none;
}
/*  ============================btns======================== */
/*  */
.button-save,
.button-form {
  width: 150px;
  padding: 10px 5px;
  margin-top: 85px;

  border-radius: 10px;
  border: none;
  background: #0ad457;

  -webkit-transition: 0.5s;

  transition: 0.5s;
}
.button-save:hover,
.button-form:hover {
  cursor: pointer;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  background: #30f17a;
}
.button-form {
  margin: 15px auto;
}
.main-select {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
.input-name {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
.input-phone {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
/*  =========================modal=========================== */
/*  */
.main-modal {
  border: solid 1px #858282;
  border-radius: 5px;

  margin: 0 auto;
  max-width: 400px;
  height: 350px;
  padding: 15px;
}
.main-modal__header {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
/* .main-modal__title {
} */
.main-modal__btn-close {
  -webkit-transition: 0.5s;
  transition: 0.5s;

  display: -webkit-box;

  display: -ms-flexbox;

  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;

  width: 30px;
  height: 30px;
}
.main-modal__btn-close svg {
  width: 15px;
  height: 15px;
}
.main-modal__btn-close:hover {
  background: #30f17a;
  border-radius: 5px;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  cursor: pointer;
}
.item-input {
  width: 70%;
  margin-bottom: 15px;
  padding: 5px;
}
.main-modal__body {
  margin-top: 25px;
}
.error-message {
  color: red;
}
/*  */
/*  =========================animate modal=========================== */
.fade-enter-active,
.fade-leave-active {
  -webkit-transition: opacity 0.5s;
  transition: opacity 0.5s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
